.App {
  background: url('https://www.aisd.net/ditto-elementary/wp-content/files/sites/59/2017/02/Chalkboard-hd-wallpapers.jpg');
  min-height: 100vh;
  object-fit: cover;
  overflow: hidden;
  text-align: center;
}

.App-header {
  align-items: center;
  background: url('https://www.aisd.net/ditto-elementary/wp-content/files/sites/59/2017/02/Chalkboard-hd-wallpapers.jpg');
  display: flex;
  flex-direction: column;
  justify-content: center;
  object-fit: cover;
}

.App-title {
  background: yellow;
  border-radius: 15px;
  border: solid 1px black;
  font-size: 3rem;
  height: 55px;
  margin-top: 20px;
  width: 50vw;
}

.app-button-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
